html {
  color-scheme: light dark;
  font-family: system-ui;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow-y: scroll;
}
.MyCoolList{
	display: flex; text-align: left;
}
/*.MyCoolList ul li*/ .hover-container{
	padding-left: 0.3vh;
	padding-bottom: 0.3vh;
	padding-top: 0.3vh;
}
.hover-container {
  position: relative;
  display: block; /* so text doesn't take full width */
  border-bottom: 1px dashed #ffffff;
  cursor: default;
  max-width: 30vw;
}
a.hover-container {
  text-decoration: none;
  cursor: pointer;
  color: inherit; /* optional: matches parent text color */
}
.hover-box {
	margin-top: 1vh;
  display: none;
  position: absolute;
  top: 100%;
  left: -1vw;
  width: 102%;
  background: #333;
  color: #fff;
  padding: 6px 10px;
  border-radius: 4px;
  z-index: 100;
  overflow-wrap: break-word;
}
.hover-box p{
  width: 100%;
  overflow-wrap: break-word;
}
.hover-container img{
	max-height: 33vh;
}

.hover-container:hover .hover-box {
  display: block;
}

a.hover-container:hover {
  max-height: 40vh;
  background-color:  #333;
  border-bottom: 2px solid #ffffff; /* or any color you want */
  color: inherit; /* optional: matches parent text color */
}